
.search {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.search .slogen {
    font-size: 36px;
    font-weight: 100;
    text-align: center;
    color: #735DEE;
    margin-top: 100px;
}
.search .video {
    font-size: 50px;
    color: #735DEE;
    -webkit-transition: all .3s;
    transition: all .3s;
}
.search .video:active {
      -webkit-transform: scale(0.96);
              transform: scale(0.96);
}
.search .search-bar {
    width: 1080px;
    background: #fff;
    height: 50px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    margin-top: 38px;
}
.search .search-bar input, .search .search-bar button {
      outline: none;
}
.search .search-bar input {
      border: none;
      width: 1020px;
      height: 50px;
      padding: 0 20px;
      font-size: 18px;
      text-align: center;
}
.search .search-bar button {
      width: 60px;
      height: 50px;
      border: none;
      background: #735DEE;
      color: #fff;
      font-size: 32px;
}
.search .search-bar button:active {
        background: #4b2fe9;
}



