
@font-face {
  font-family: 'Roboto';
  src: url("./assets/fonts/Roboto-Regular.woff") format("woff"), url("./assets/fonts/Roboto-Regular.woff2") format("woff2"), url("./assets/fonts/Roboto-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'iconfont';
  /* project id 387362 */
  src: url("//at.alicdn.com/t/font_387362_hqt0js5zozaaif6r.eot");
  src: url("//at.alicdn.com/t/font_387362_hqt0js5zozaaif6r.eot?#iefix") format("embedded-opentype"), url("//at.alicdn.com/t/font_387362_hqt0js5zozaaif6r.woff") format("woff"), url("//at.alicdn.com/t/font_387362_hqt0js5zozaaif6r.ttf") format("truetype"), url("//at.alicdn.com/t/font_387362_hqt0js5zozaaif6r.svg#iconfont") format("svg");
}
.iconfont {
  font-family: "iconfont" !important;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -webkit-text-stroke-width: 0.2px;
  -moz-osx-font-smoothing: grayscale;
}
::-webkit-input-placeholder {
  color: #efefef;
}
body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto';
  -webkit-user-select: none;
  -webkit-font-smoothing: antialiased;
}
html {
  background: #343638;
  width: 100%;
  height: 100%;
}
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
}
.swiper-container-horizontal >
.swiper-pagination-bullets .swiper-pagination-bullet {
  background: #735DEE;
}
.bgmov {
  position: fixed;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  z-index: -100;
  background-size: cover;
}
.bgCover {
  background: url(./assets/bg.png) center;
  position: fixed;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  z-index: -99;
}
.footer {
  position: absolute;
  bottom: 12px;
  color: #767676;
  font-size: 10px;
  text-align: center;
  width: 100%;
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}



