
.article {
  width: 1080px;
  margin: 80px auto;
  position: relative;
}
.article::before {
    content: 'Articles';
    color: #959EA8;
    position: absolute;
    font-size: 24px;
    top: -40px;
}
.article .article-cate {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.article .article-cate li {
      width: 160px;
      height: 78px;
      margin-right: 8px;
      position: relative;
      cursor: pointer;
      -webkit-transition: all .3s;
      transition: all .3s;
}
.article .article-cate li:active {
        -webkit-transform: scale(0.96);
                transform: scale(0.96);
}
.article .article-cate li span {
        position: absolute;
        font-size: 12px;
        color: #fff;
        left: 6px;
        bottom: 6px;
}
.article .article-cate .Arthropoda {
      background: url(../assets/cover/1.png) no-repeat;
      background-size: 100%;
}
.article .article-cate .Snake {
      background: url(../assets/cover/2.png) no-repeat;
      background-size: 100%;
}
.article .article-cate .Turtle {
      background: url(../assets/cover/3.png) no-repeat;
      background-size: 100%;
}
.article .article-cate .Amphibian {
      background: url(../assets/cover/4.png) no-repeat;
      background-size: 100%;
}
.article .article-cate .Lizards {
      background: url(../assets/cover/5.png) no-repeat;
      background-size: 100%;
}



