
.modal-cover {
  width: 100%;
  height: 100%;
  position: fixed;
  background: rgba(28, 31, 36, 0.72);
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 100;
  overflow-y: scroll;
}
.modal {
  width: 800px;
  background: #fff;
  position: absolute;
  left: 50%;
  margin-left: -400px;
  top: 100px;
  z-index: 200;
}
.modal .close {
    width: 50px;
    height: 50px;
    display: block;
    position: absolute;
    right: 0;
    top: 0;
    z-index: 110;
    color: #fff;
    font-size: 32px;
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    background: #735DEE;
}
.modal .modal-title {
    height: 56px;
    border-bottom: 1px solid #D6D6D6;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    padding-left: 20px;
}
.modal .modal-title h5 {
      font-size: 24px;
}
.modal table {
    width: 100%;
    border-collapse: separate;
    border-spacing: 0 0;
    font-size: 14px;
}
.modal table .table-num {
      font-size: 20px;
}
.modal table tr td {
      border-bottom: 1px solid #D6D6D6;
      border-right: 1px solid #D6D6D6;
      text-align: left;
      padding: 10px 10px;
}
.modal table tr td:nth-child(2n-1) {
        font-weight: 600;
}
.modal .mapbox {
    outline: none;
    height: 300px;
    width: 100%;
}
.modal .mapbox .mapboxgl-canvas {
      width: 100%;
}
.modal .content {
    padding: 20px;
    font-size: 14px;
    color: #4B4F57;
    letter-spacing: 0.6px;
    line-height: 24px;
    border-bottom: 1px solid #D6D6D6;
}
.modal .content p {
      margin: 0;
}
.modal .vue-echarts {
    width: 320px;
    height: 300px;
    position: absolute;
    top: 10px;
    left: 10px;
    z-index: 120;
}



