
.tags {
  width: 1080px;
  margin: 80px auto 0 auto;
  position: relative;
}
.tags::before {
    content: 'Arthropoda Category';
    color: #959EA8;
    position: absolute;
    font-size: 24px;
    top: -40px;
}
.tags li {
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    background: #363D4C;
    color: #959EA8;
    font-size: 14px;
    height: 30px;
    padding: 0 12px;
    margin-top: 6px;
    margin-right: 6px;
    -webkit-transition: all .3s;
    transition: all .3s;
    cursor: pointer;
}
.tags li:hover {
      background: #fff;
      color: #735DEE;
}
.tags li:active {
      background: #4b2fe9;
}
.tags li span {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-pack: center;
          -ms-flex-pack: center;
              justify-content: center;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      padding: 0 0 0 10px;
      height: 30px;
      margin-left: 10px;
      border-left: 1px solid #2D3239;
}



